export default function (result) {
    let commission = result.commission || { //
        "agent_level1": { //
            "nickname": "", //
            "avatar": "", //
            "level": "", //
            "mobile": "", //
            "commission": "" //
        },
        "agent_level2": { //
            "nickname": "",
            "avatar": "",
            "level": "",
            "mobile": "",
            "commission": ""
        },
        "agent_level3": { //
            "nickname": "",
            "avatar": "",
            "level": "",
            "mobile": "",
            "commission": ""
        },
        "commission_status": -1 // 0 1
    }; //
    console.log(commission)
    let statusImages = {
        "-2": '/static/img/global/order_detail_state6.png', // -2
        "-1": "/static/img/global/order_detail_state4.png", //-1
        "0": "/static/img/global/order_detail_state3.png", //  0
        "1": "/static/img/global/order_detail_state1.png", // 1
        "2": "/static/img/global/order_detail_state2.png", // 2
        "3": "/static/img/global/order_detail_state5.png", // 3
    }
    let commisionState = ['', '']; //
    let extra_price_package = result.order.extra_price_package || { //
        full: 0, //
        member_discount: 0, //
        member_price: 0 //
    };
    let refunding = 0; //// 0  1  2 
    return {
        billInfo1: {
            billStatusText: result.order.status_text, //
            billPrice: result.order.pay_price, //
            billStatus: result.order.status, //  -2-1 0123
            image: statusImages[result.order.status]
        },
        billInfo2: {
            billId: result.order.order_no, //
            billStatusText: result.order.status_text, //
            billStatus: result.order.status, // -2-1 0123
            billTypeText: result.order.type_text, //  1 2 3  4 5
            billOriginText: result.order.create_from_text, //
            billOrigin: result.order.create_from, // 0:  1:  2: wap/h5 3: app 4 :pc
            payTypeText: result.order.pay_type_text, // 0  1 2  3  10  20 30 
            payType: result.order.pay_type, //
        },
        billInfo3: {
            billTime: result.order.create_time, //
            payTime: result.order.pay_time, //
            receiveTime: result.order.finish_time, //
            payTypeText: result.order.pay_type_text, // 0  1 2  3  10  20 30 
            payType: result.order.pay_type, //
            billStatus: result.order.status, // -2-1 0123
        },
        billInfo4: {
            buyer: result.order.member_nickname, //
            addtion: result.order.remark_buyer, //
            provideTypeText: result.order.dispatch_type_text, //
            provideType: result.order.dispatch_type_text, // 0  1 2 3
            receiver: result.order.buyer_name, //
            address: result.order.address_full //
        },
        billInfo5: {
            moneyState: commission.commission_status == -1 ? -1 : commisionState[commission.commission_status], // 0 1  
            firstOne: {
                name: commission.agent_level1 && commission.agent_level1.nickname || '',
                tel: commission.agent_level1 && commission.agent_level1.mobile || '',
                money: commission.agent_level1 && commission.agent_level1.commission || '',
            }, //
            secondOne: {
                name: commission.agent_level2&&commission.agent_level2.nickname||'',
                tel: commission.agent_level2&&commission.agent_level2.mobile||'',
                money: commission.agent_level2&&commission.agent_level2.commission||''
            }, //
            thirdOne: {
                name: commission.agent_level3&&commission.agent_level3.nickname||'',
                tel: commission.agent_level3&&commission.agent_level3.mobile||'',
                money: commission.agent_level3&&commission.agent_level3.commission||''
            } //
        },
        billInfo6: [...result.goods_waits.map(item => { //
            if (item.refund_status != 0) {
                refunding = item.refund_status; //
            }
            return {
                goodsInfo: [{ //
                    img: item.thumb, //
                    goodName: item.title, //
                    color: item.option_title,
                    size: '',
                    num: item.total, //
                    price: item.price_unit, //
                    discount: item.price_discount, //
                    total: item.price_original, //
                    refund_status: item.refund_status, // 0  1  2 
                    refund_id: item.refund_id, //id 0
                }],
                billInfo: { // 
                    sendTime: '', //
                    sendComp: '', //
                    sendId: '', //
                    sendStatus: '' //
                }
            }
        }), ...result.package_send.map(item => { //

            return {
                goodsInfo: item.order_goods.map((val, key) => { //
                    if (val.refund_status != 0) {
                        refunding = val.refund_status; //
                    }
                    return {
                        img: val.thumb, //
                        goodName: val.title, //
                        color: val.option_title,
                        size: '',
                        num: val.total, //
                        price: val.price_unit, //
                        discount: val.price_discount, //
                        total: val.price_original, //
                        refund_status: val.refund_status,
                        refund_id: val.refund_id
                    }
                }),
                billInfo: { // 
                    sendTime: item.send_time, //
                    sendComp: item.express_name, //
                    sendId: item.express_sn, //
                    sendStatus: '' //
                }
            }
        })],
        billInfo7: {
            goodTotal: result.order.goods_price, //
            vipCount: extra_price_package.member_discount||0, //
            sendCost: result.order.dispatch_price, //
            total: result.order.pay_price, //
            rightStatus: refunding // // 0  1  2 
        },
    }
}