import {
    resolve
} from "url";

export default function (search = {
    status: 1, //1 3 -2 -1
    title: '',
    category_ids: '',
    group_ids: '',
    type: '', //123
    goods_sort: '',
    goods_by: '',
    pagesize: 20,
    page: 1
}) {

    let list = []
    return new Promise((resolve, reject) => {
        let status = 0;
        this.Request('goodsList', search).then(res => {
            if (search.status == 1) {
                status=0;
            } else if (search.status == 3) {
                status=1;
            } else if (search.status == -2) {
                status=2;
            } else if (search.status == -1) {
                status=3;
            }
            list = res.list.map(item => {
                return {
                    img: item.thumb,
                    goodName: item.title,
                    color: item.sub_title,
                    size: '',
                    num: item.stock, //
                    price: item.price, //
                    saled: item.sales_count, //
                    status: status, //0,1,2,3
                    goodId: item.id,
                    has_option: item.has_option, //

                }
            })

            resolve(list)
        })
    });
}