export default function (val, detail) {
    let DataGo = ''
    if (val.label == '') {
        DataGo = 'editName'
    } else if (val.label == '') {
        DataGo = 'editSubTitle'
    } else if (val.label == '') {
        DataGo = 'selectType'
    } else if (val.label == '') {
        DataGo = 'editCode'
    } else if (val.label == '') {
        DataGo = 'setFreight'
    } else if (val.label == '') {
        DataGo = 'editForm'
    } else if (val.label == '') {
        DataGo = 'editStatus'
    } else if (val.label == '') {
        DataGo = 'editMultiCode'
    } else if (val.label == '') {
        DataGo = 'autoDeliverContent'
    }

    this.Cacher.setData('editGood', {
        from: this.Cacher.getData('editGood').from,
        go: DataGo,
        detail,
        needChange: val
    })
    uni.navigateTo({
        url: '../pages/' + DataGo + '?from=editGood'
    })
}