function judgeState(status, is_checked) {
    function check() { 
        if (status == 0) { //(0 1 -1 -2)
            return {
                num: 1,
                text: ''
            }; //
        } else if (status == 1) { //1
            return {
                num: 0,
                text: ''
            }; //
        } else if (status == -1) { // -1 
            return {
                num: 2,
                text: ''
            }; //
        } else if (status == -2) { //-2
            return {
                num: 5,
                text: ''
            }; //
        }
    }

    if (is_checked == 1) { //(1 0 -1)
        return check();
    } else if (is_checked == 0) { //0
        return {
            num: 3,
            text: ''
        }
    } else if (is_checked == -1) { //-1
        return {
            num: 4,
            text: ''
        }
    }

}
export default function (list) {
    return list.map(item => {
        let tmp = judgeState(item.status, item.is_checked);
        return {
            title: item.name,
            left: item.days + '',
            status: tmp.num,
            statusText: tmp.text,
            img: item.logo || '/static/img/global/tmpAct.png',
            shopInfo:item
        }
    })
}