export default function (tabid, data) {

    let billtype = ['waitPayBill', 'waitProvideBill', 'waitReceiveBill', 'finishedBill', 'closedBill', ];

    return new Promise((resolve, reject) => {
        this.Request(billtype[tabid], data).then(res => {
            let list = [];
            let goodlist = [];
            let countGood = 0;
            this.totalPage = Math.max(Math.ceil(res.count / 20), 1);
            list = res.list.map(item => {
                goodlist = item.order_goods || [];
                goodlist.forEach(item => {
                    countGood += item.total;
                })
                return {
                    info: { //
                        name: item.member_nickname, //
                        provide: item.dispatch_type_text, //
                        num: countGood, //
                        pay: item.pay_price, //
                        addtion: item.remark_num, //
                        payType: item.pay_type, //
                        subStatus: item.is_refund, //// 0  1  2 
                        status: tabid, //0,1234
                    },
                    bill: { //
                        billId: item.order_no, //
                        billDate: item.create_time, //
                        billType: item.type_text, //
                        billPrice: item.pay_price,
                        id: item.id,//id
                    },
                    goodsList: goodlist.map(item => { //
                        return {
                            img: item.thumb, //
                            goodName: item.title, //
                            color: item.option_title ? item.option_title : '', //,
                            size: '', //
                            num: item.total, //
                            price: item.price_unit, //
                            specifications: item.option_title ? 'multi' : 'single', //,
                            id: item.id,
                        }
                    }),
                    rights: { // 
                        status: '', //
                        addition: item.remark_num, //
                        subStatus: item.is_refund, // //// 0  1  2 
                    }
                };
            });
            resolve(list);
        })
    })
}